@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

html {
  font-size: 12px;
  background: #242e42;
}

.App {
  display: flex;
  flex-direction: column;
  place-items: center;
  height: 100vh;
  width: 100vw;
  background: #242e42;
}

#game-wrapper {
  display: grid;
  grid-template-rows: 1fr 9fr;
  width: 100%;
  place-content: center;
  color: #9a2881;
  text-shadow: 1px 1px 0px black;
  font-family: "Press Start 2P", cursive;
  text-align: center;
}

#header-wrapper {
  align-self: center;
  font-size: calc((0.2em + 1.6vmin) + (0.2em + 1.6vmax));
  color: #9a2881;
  text-shadow: 2px 2px 0px #9a8228;
  font-family: "Press Start 2P", cursive;
  text-align: center;
}

#board-wrapper {
  display: flex;
  flex-direction: column;
  place-items: center;
  align-self: center;
  padding-bottom: 10px;
  width: 100vw;
}

#board-wrapper__player-board-wrapper {
  display: grid;
  grid-column: 1;
  grid-row: 1;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  height: 300px;
  width: 300px;
}

#board-wrapper__player-board-wrapper--preparing {
  display: grid;
  grid-column: 1;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  height: 300px;
  width: 300px;
}

#info-area {
    display: flex;
    flex-direction: column;
    grid-column: 2;
    grid-row: 1;
    margin-top: 10px;
    margin-bottom: 10px;
    place-items: center;
    justify-content: space-evenly;
    font-size: calc(.4em + 1vmin + 1vmax);
    height: 100%;
    width: 100%;
}

#turn-display{
  margin-bottom: 10px;
}

#cat-face-wrapper {
  display: flex;
  width: 100%;
  align-items: center;
  place-content: center;
}

#cat-face {
  display: grid;
  grid-template-rows: 2fr 1fr;
  place-items: center;
}

#cat-lives-wrapper {
  display: grid;
  width: 45%;
  justify-content: center;
  place-items: center;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
}

.cat-lives-wrapper__heart {
  width: 3vw;
  max-width: 110px;
  min-width: 25px;
}

#cat-face-wrapper__cat-face {
  margin-bottom: 10px;
  width: 10vw;
  height: auto;
  min-width: 100px;
}

#board-wrapper__computer-board-wrapper {
  display: grid;
  grid-column: 3;
  grid-row: 1;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  height: 300px;
  width: 300px;
}

.empty-block {
  background: #536996;
  box-shadow: 0px 0px 0px 1px #242e42;
  cursor: pointer;
}

.empty-block--hovered {
  background: #1c6c67;
  box-shadow: 0px 0px 0px 1px #242e42;
}

.empty-block--color {
  background: #536996;
  box-shadow: 0px 0px 0px 1px #242e42;
  cursor: pointer;
  transition: all ease-in-out;
}

.empty-block--color:hover {
  background: #1c6c67;
}

.empty-block--hovered--not-allowed {
  background: #536996;
  box-shadow: 0px 0px 0px 1px #242e42;
  cursor: not-allowed;
}

.ship-block {
  background: #f51743;
  box-shadow: 0px 0px 0px 1px #242e42;
}

.player-ship-block {
  box-shadow: 0px 0px 0px 1px #242e42;
  background: #289a88;
  height: 100%;
  cursor: not-allowed;
}

.player-ship-block--removable {
  box-shadow: 0px 0px 0px 1px #242e42;
  background: #289a88;
  height: 100%;
  cursor: pointer;
}

.player-ship-block--hit {
  display: flex;
  place-items: center;
  place-content: center;
  box-shadow: 0px 0px 0px 1px #242e42;
  background: #9a2881;
  height: 100%;
}

.player-ship-block--sunk {
  box-shadow: 0px 0px 0px 1px #242e42;
  background: #9a8228;
  height: 100%;
}

.miss-block {
  box-shadow: 0px 0px 0px 0.5px #242e42;
}

.invisible-block {
  background: #536996;
  box-shadow: 0px 0px 0px 1px #242e42;
  cursor: pointer;
}

.enemy-ship-block {
  background: #536996;
  box-shadow: 0px 0px 0px 1px #242e42;
  cursor: pointer;
}

.enemy-ship-block--color {
  background: #536996;
  box-shadow: 0px 0px 0px 1px #242e42;
  cursor: pointer;
  transition: all ease-in-out;
}

.enemy-ship-block--color:hover {
  background: #1c6c67;
  box-shadow: 0px 0px 0px 1px #242e42;
  cursor: pointer;
}

.enemy-ship-block--hit {
  display: flex;
  place-items: center;
  place-content: center;
  background: #9a2881;
  box-shadow: 0px 0px 0px 1px #242e42;
  height: 100%;
}

.enemy-ship-block--sunk {
  background: #9a8228;
  box-shadow: 0px 0px 0px 1px #242e42;
  height: 100%;
}

#player-ships-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  place-content: center;
  grid-column: 3;
  grid-row: 1;
  height: 100%;
  width: auto;
  max-width: 300px;
  flex-wrap: wrap;
  padding: 3%;
  box-shadow: 0px 0px 20px 1px #ffe9f3;
}

#player-ships-wrapper__ships {
  display: flex;
  flex-flow: wrap;
  width: 100%;
  place-content: center;
}

#player-ships-wrapper__ships__horizontal {
  display: flex;
  flex-direction: column;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 1%;
}

#player-ships-wrapper__ships__vertical {
  display: flex;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 1%;
}

.ship {
  background: #536996;
  box-shadow: 0px 0px 0px 1px #242e42;
}

#player-ships-wrapper__instructions-wrapper {
  display: flex;
  flex-direction: column;
  grid-row: 1;
}

.ship__rotate-ship-button {
  grid-row: 1;
  border: none;
  outline: none;
  align-self: center;
  height: 30px;
  width: 30px;
  display: flex;
  align-self: center;
  justify-content: center;
  cursor: pointer;
  background: #9a2881;
}

.rotate-ship-button__img {
  align-self: center;
  width: 100%;
}

.player-ships-wrapper__ship--horizontal {
  display: flex;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.player-ships-wrapper__ship--vertical {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.ship-highlight {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 1%;
  cursor: pointer;
  background: #289a88;
  box-shadow: 0px 0px 0px 1px #242e42;
}

.ship-highlight--selected {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 1%;
  background: #289a88;
  box-shadow: 0px 0px 3px 2px #f51743;
}

.player-ship__ship-blocks {
  display: flex;
}

.player-ship-block--docked {
  height: 30px;
  width: 30px;
  box-shadow: 0px 0px 0px 1px #242e42;
  pointer-events: none;
}

#instructions-wrapper__instructions {
  font-size: calc((0.05em + 0.6vmin) + (0.05em + 1vmax));
}

#instructions-wrapper__instructions--green{
  color: #289a88;
}

#player-ships-wrapper__auto-place-wrapper {
  display: grid;
  grid-gap: 10px;
  flex-direction: column;
  place-items: center;
  grid-row: 3;
  font-size: calc((0.05em + 0.6vmin) + (0.05em + 1vmax));
}

#player-ships-wrapper__auto-place-button {
  background: #289a88;
  width: -webkit-min-content;
  width: -moz-min-content;
  width: min-content;
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 10px;
  padding-right: 10px;
  font-size: calc((0.05em + 0.5vmin) + (0.05em + 0.8vmax));
  cursor: pointer;
}

#player-ships-wrapper__auto-place-button--color {
  background: #289a88;
  width: -webkit-min-content;
  width: -moz-min-content;
  width: min-content;
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 10px;
  padding-right: 10px;
  cursor: pointer;
  font-size: calc((0.05em + 0.5vmin) + (0.05em + 0.8vmax));
  transition: all 0.1s ease-in-out;
}

#player-ships-wrapper__auto-place-button--color:hover {
  background: #1c6c67;
}

.error-wrapper {
  grid-column: 2;
}

#start-game-button {
  grid-column: 2;
  background: #289a88;
  width: -webkit-min-content;
  width: -moz-min-content;
  width: min-content;
  margin: 20px;
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 10px;
  padding-right: 10px;
  font-size: calc((0.05em + 0.5vmin) + (0.05em + 0.8vmax));
  cursor: pointer;
}

#start-game-button--color {
  grid-column: 2;
  background: #289a88;
  width: -webkit-min-content;
  width: -moz-min-content;
  width: min-content;
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 10px;
  padding-right: 10px;
  font-size: calc((0.05em + 0.5vmin) + (0.05em + 0.8vmax));
  cursor: pointer;
  transition: all 0.1s ease-in-out;
}

#start-game-button--color:hover {
  background: #1c6c67;
}

#game-over-info {
  display: flex;
  flex-direction: column;
}

#game-over-info__restart-game-button {
  background: #289a88;
  width: -webkit-min-content;
  width: -moz-min-content;
  width: min-content;
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 10px;
  padding-right: 10px;
  font-size: calc((0.05em + 0.5vmin) + (0.05em + 0.8vmax));
  cursor: pointer;
  grid-row: 3;
}

#game-over-info__restart-game-button--color {
  background: #289a88;
  width: -webkit-min-content;
  width: -moz-min-content;
  width: min-content;
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 10px;
  padding-right: 10px;
  font-size: calc((0.05em + 0.5vmin) + (0.05em + 0.8vmax));
  cursor: pointer;
  grid-row: 3;
  transition: all 0.1s ease-in-out;
}

#game-over-info__restart-game-button--color:hover {
  background: #1c6c67;
}

@media only screen and (min-width: 1023px) {
  #game-wrapper {
    display: grid;
    grid-template-rows: 1fr 11fr;
    width: 100%;
    height: 100%;
    place-content: center;
    color: #9a2881;
    text-shadow: 1px 1px 0px black;
    font-family: "Press Start 2P", cursive;
    text-align: center;
  }

  #board-wrapper {
    display: grid;
    grid-gap: 5px;
    grid-template-columns: 1fr 1fr 1fr;
    place-items: center;
    align-self: center;
  }

  #board-wrapper__player-board-wrapper {
    display: grid;
    grid-column: 1;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
    height: 31vw;
    width: 31vw;
  }

  #board-wrapper__player-board-wrapper--preparing {
    display: grid;
    grid-column: 2;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
    height: 31vw;
    width: 31vw;
    min-height: 450px;
    min-width: 450px;
  }

  #cat-face-wrapper__cat-face {
    margin-bottom: 10px;
    width: 10vw;
    height: auto;
  }

  .cat-face__speech {
    display: flex;
    font-size: calc((0.05em + 0.6vmin) + (0.05em + 0.5vmax));
  }


  #cat-lives-wrapper {
    width: 60%;
  }

  #board-wrapper__computer-board-wrapper {
    display: grid;
    grid-column: 3;
    grid-row: 1;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
    height: 31vw;
    width: 31vw;
  }

  #player-ships-wrapper {
    display: grid;
    grid-gap: 10px;
    grid-template-rows: auto auto auto;
    flex-direction: column;
    align-items: center;
    height: 100%;
    grid-column: 3;
    grid-row: 1;
    flex-wrap: wrap;
    justify-content: space-around;
    max-width: 30vw;
    min-height: 500px;
  }

  .ship__rotate-ship-button {
    height: 3.1vw;
    width: 3.1vw;
    min-height: 45px;
    min-width: 45px;
  }

  .player-ship-block--docked {
    height: 3.1vw;
    width: 3.1vw;
    min-height: 45px;
    min-width: 45px;
    box-shadow: 0px 0px 0px 1px #242e42;
    pointer-events: none;
  }

  .player-ships-wrapper__ship--horizontal {
    display: flex;
    flex-direction: row;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
  }

  .player-ships-wrapper__ship--vertical {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
  }
}

